import React, { useState, useEffect } from 'react'
import './Einkauf.css'

import useFetch from '../hooks/useFetch'

const Einkauf = () => {

    const [items, setItems] = useState(null)
    const [recipes, setRecipes] = useState(null)
    const [categories, setCategories] = useState(null)
    const [active, setActive] = useState(false)

    const res = useFetch('get_shopping',
        {
            method: 'GET',
            headers: { 'Accept': 'application/json' },
        },
        "Shopping"
    );

    const data = res.response

    useFetch('edit_shopping',
        {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                clear: true
            }),
        },
        "Update",
        active
    )

    const recipe_items = recipes ? recipes.map((x, index) => {
        return (
            <div key={index}>{x.recipe_name}</div>
        )
    }) : null

    const list_items = items ? items.map((x, index) => {
        return (
            <div>
                <div key={'categorie_item_' + index}>{x.category}</div>
                {x.items.map((y, index2) => {
                    return (
                        <div key={'ingredient_item_' + index2}>{y.item}</div>
                    )
                })}
            </div>
        )
    }) : null

    useEffect(() => {
        if (data) {
            setItems(data.ingredients)
            setRecipes(data.recipes)
        }
    }, [data]);

    return (
        <div className="einkauf__container" onClick={() => {
            setActive(true)
            setRecipes([])
            setItems([])
        }}>
            <div>
                {recipe_items}
            </div>
            <div>
                {list_items}
            </div>
        </div>
    )
}

export default Einkauf